import styled, { css } from 'styled-components';

import { buildStyledComponent } from 'utils';

const modifiers = {
  padRight: () => ({
    styles: css`
      padding-right: 4px;
    `,
  }),
};

const styles = ({ theme }) => css``;

export default buildStyledComponent('Icon', styled.i, styles, { modifiers });



